import Vue from 'vue';
import Router from 'vue-router';
import Splash from '../../components/pages/splash.vue';
import Home from '../../components/pages/home.vue';
import About from '../../components/pages/about.vue';
import Vision from '../../components/pages/vision.vue';
import Mission from '../../components/pages/mission.vue';
import Services from '../../components/pages/services.vue';
import ServiceDetails from '../../components/pages/service-details.vue';
import Why from '../../components/pages/why.vue';
import Capacity from '../../components/pages/capacity.vue';
import Gallery from '../../components/pages/gallery.vue';
import Contact from '../../components/pages/contact.vue';
Vue.use(Router);

const routes = [{
        path: "/",
        name: 'splash',
        component: Splash,
    },
    {
        path: "/home",
        name: 'home',
        component: Home,
    },
    {
        path: "/about",
        name: 'about',
        component: About,
    },
    {
        path: "/vision",
        name: 'vision',
        component: Vision,
    },
    {
        path: "/mission",
        name: 'mission',
        component: Mission,
    },
    {
        path: "/services",
        name: 'services',
        component: Services,
    },
    {
        path: '/services/:id',
        name: 'serviceDetails',
        component: ServiceDetails,
    },
    {
        path: "/why",
        name: 'why',
        component: Why,
    },
    {
        path: "/capacity",
        name: 'capacity',
        component: Capacity,
    },
    {
        path: "/gallery",
        name: 'gallery',
        component: Gallery,
    },
    {
        path: "/contact",
        name: 'contact',
        component: Contact,
    },
    {
        path: '*',
        redirect: '/'
    }
]

export default new Router({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes,
})